.App {
  text-align: center;
  margin-top: 30px;
}

header{
	font-size: 35px;
	margin: 20px;
}

input[type=search]::-webkit-search-cancel-button {
    -webkit-appearance: searchfield-cancel-button;
}

input {
	width: 50%;
	padding-left: 10px;
	padding-right: 10px;
	border: none;
	box-shadow: 0 2px 2px 0 rgba(0,0,0,0.16),0 0 0 1px rgba(0,0,0,0.08);
}

.book-display {
	display: flex;
	margin-top: 30px;
	flex-wrap: wrap;
	justify-content: center;
}

.book-card {
	position: relative;
	width: 400px;
	height: 300px;
	margin: 10px;
	text-align: left;
}

.book-img {
	position: absolute;
	width: 140px;
    height: 200px;
}

.book-info {
	position: absolute;
	left: 150px;
	font-size: 14px;
}

.book-title {
	font-size: 18px;
}

.book-link {
	position: absolute;
	top: 150px;
	width: 130px;
	height: 35px;
	border-radius: 10px;
}

